import React from 'react'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'
import { auth } from '../store'
import { withRouter } from 'react-router'

/**
 * COMPONENT
 */

const NameForm = () => (<div>
  <label htmlFor="name"><small>Name</small></label>
  <input name="name" type="text" />
</div>)


const AuthForm = (props) => {
  const {name, displayName, handleSubmit, error} = props

  return (
    <div>
    <h3> {name}</h3>

      <form onSubmit={handleSubmit}>

        {
          name === 'signup'
          ? <NameForm />
          : ''
          
        }

        <div>
          <label htmlFor="email"><small>Email</small></label>
          <input name="email" type="text" />
        </div>
        <div>
          <label htmlFor="password"><small>Password</small></label>
          <input name="password" type="password" />
        </div>
        <div>
          <button type="submit">{displayName}</button>
        </div>
        {error && error.response && <div> {error.response.data} </div>}
      </form>
      <a href="/auth/google">{displayName} with Google</a>
    </div>
  )
}

/**
 * CONTAINER
 *   Note that we have two different sets of 'mapStateToProps' functions -
 *   one for Login, and one for Signup. However, they share the same 'mapDispatchToProps'
 *   function, and share the same Component. This is a good example of how we
 *   can stay DRY with interfaces that are very similar to each other!
 */
const mapLogin = (state) => {
  return {
    name: 'login',
    displayName: 'Login',
    error: state.user.error
  }
}

const mapSignup = (state) => {
  return {
    name: 'signup',
    displayName: 'Sign Up',
    error: state.user.error
  }
}

const mapDispatch = (dispatch) => {
  //console.log("state----->", state, this.state )
  return {
    handleSubmit (evt) {
      evt.preventDefault()
      const userName = evt.target.name.value
      const email = evt.target.email.value
      const password = evt.target.password.value
      const method = !userName ? 'login': 'signup'
      dispatch(auth(email, password, userName, method))
    }
  }
}

export const Login = withRouter(connect(mapLogin, mapDispatch)(AuthForm))
export const Signup = withRouter(connect(mapSignup, mapDispatch)(AuthForm))

/**
 * PROP TYPES
 */
AuthForm.propTypes = {
  name: PropTypes.string.isRequired,
  displayName: PropTypes.string.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  error: PropTypes.object
}



